      },
      checkServerResponse : function (response, authFlg) {
        //サーバー返答時の共通処理
        this.$root.log(response.data);
        let resData = response.data;
        if (resData["status"] == this.API_FAILED_AUTH) {
          //未認証エラー
          this.isLogin = false;
          this.userData = {
            userId : "",
            userName : "",
            profile : "",
            isAuthMail : false,
            isAuthTwitter : false,
            isDispR18 : false,
            isDispR18g : false,
          }

          //認証が必要なページはトップに戻す
          if (authFlg) {
            this.$router.push("/");
          }
          
          return false;
        } else if (resData["status"] == this.API_FAILED_PUBLISHING) {
          //画面で制御
          return true;
        } else if (resData["status"] != this.API_SUCCESS) {
          //エラー
          this.$router.push({path : "/error", query : {errorCode : resData["status"], message : resData["errMsg"]}});
          return false;
        }

        if (resData["baseInfo"] != null) {
          //ログイン時
          //基本情報を設定
          this.isLogin = true;
          let baseData = resData["baseInfo"];
          this.userData = {
            userId : baseData["user_id"],
            userName : baseData["user_name"],
            profile : baseData["profile"],
          }
          
          if (baseData["auth_mail"] == "0") {
            this.userData.isAuthMail = false;
          } else {
            this.userData.isAuthMail = true;
          }

          if (baseData["auth_twitter"] == "0") {
            this.userData.isAuthTwitter = false;
          } else {
            this.userData.isAuthTwitter = true;
          }
          
          if (baseData["disp_r18"] == "0") {
            this.userData.isDispR18 = false;
          } else {
            this.userData.isDispR18 = true;
          }

          if (baseData["disp_r18g"] == "0") {
            this.userData.isDispR18g = false;
          } else {
            this.userData.isDispR18g = true;
          }
        } else {
          //非ログイン
          this.isLogin = false;
          this.userData = {
            userId : "",
            userName : "",
            profile : "",
            isAuthMail : false,
            isAuthTwitter : false,
            isDispR18 : false,
            isDispR18g : false,
          }
        }
        return true;
      },
      comNetworkError : function(error) {
        this.$root.log(error.response);
        this.$router.push({path : "/error", query : {errorCode : this.API_FAILED_EXEPTION, message : ""}});
      },
      pushHome : function () {
        if (this.isLogin) {
          this.$router.push("/mainmenu");
        } else {
          this.$router.push("/");
        }
      },
      pushBtn : function (name) {
        this.pushMenu();
        if (name == "search") {
          this.$router.push("/search");
        } else if (name == "latest") {
          this.$router.push("/latest");
        }  else if (name == "edit") {
          this.$router.push("/edit/new");
        } else if (name == "postlist") {
          this.$router.push({ path:"/postlist", query : {series : "すべて", page : 1}});
        } else if (name == "bookmark") {
          this.$router.push({ path:"/bookmark", query : {series : "すべて", page : 1}});
        } else if (name == "follow") {
          this.$router.push({ path:"/follow", query : {page : 1}});
        } else if (name == "setting") {
          this.$router.push("/setting");
        } else if (name == "usage") {
          this.$router.push("/usage");
        } else if (name == "about") {
          this.$router.push("/about");
        } else if (name == "terms") {
          this.$router.push("/terms");
        } else if (name == "privacy") {
          this.$router.push("/privacypolicy");
        } else if (name == "contact") {
          this.$router.push("/contact");
        } else if (name == "info") {
          this.$router.push("/infomation");
        } else if (name == "guidelines") {
          this.$router.push("/guidelines");
        }
      },
      pushMenu : function () {
        if (this.navClass == '') {
          this.navClass = 'panelactive';
        } else {
          this.navClass = '';
        }
      },
      Logout : function (flg) {
        this.$root.log("Logout");
        if (flg) {
          this.pushMenu();
        }

        axios.get(this.CSRF_URL).then(response => {
          this.$root.log(response);
          axios.get(this.$root.SERVER_PATH + 'logout')
          .then((response) => {
            let resData = response.data;
            this.$root.log(resData["status"] );
          
            if (!this.$root.checkServerResponse(response, true)) {
              return;
            }
            
            signOut(getAuth());
            this.userData = {
              userId : "",
              userName : "",
              profile : "",
            };
            this.$router.push('/');
            this.isLogin = false;
          })
          .catch((error) => {
            this.$root.comNetworkError(error);
          });
        });
      },
      getBaseInfo : function() {
        axios.get(this.$root.SERVER_PATH + 'getBaseInfo')
        .then((response) => {
          console.log(response.data);
          if (!this.checkServerResponse(response, false)) {
            return;
          }
        });
      },
      arrangeStr(value, splitStr) {
        if (value == "") {
          return value;
        }

        //検索の場合
        if (splitStr == "　") {
          //半角スペースの場合は検索の処理
          this.setMuteTag = this.setMuteTag.replace("　", "");
        }
        let retStr = "";
        let strArray = value.split(splitStr);
        for (let i = 0; i < strArray.length; i++) {
          let val = strArray[i].trim();
          if (val != "") {
            retStr = retStr + val;

            if (i != strArray.length - 1) {
              retStr = retStr + splitStr;
            }
          }
        }
        return retStr;
