      },
      pushTips: function(index) {
        if (index == "EDIT_CONVERSION") { this.showPopup("TIPS", "▼変換項目", "◆項目説明\n変換する文字の項目についての説明文を20文字以内で入力してください。\n\n入力例）\n名字は？\n名前は？\n\n◆変換文字\n変換する文字を10文字以内で入力してください。\n\n入力例）\n夢野\nアメ\n\n◆備考\n「×」アイコンを押すと、変換項目を減らすことができます。\n「＋項目を追加」で変換項目を最大４つまで増やすことができます。\n\n変換項目、変換文字ともに空欄だった場合、あるいは変換項目を「×」ですべて削除した場合は、自動的に「名前変換なし」の作品として扱われます。");
        } else if (index == "EDIT_SERIES") { this.showPopup("TIPS", "▼シリーズ", "複数の投稿作品を１つのシリーズにまとめることができます。\n長編タイトルや作品傾向、短編集など、好きなワードでまとめてください。\n\n◆備考\n前後に続かない短編作品の場合、シリーズを指定せずに投稿することができます。\nこちらは便利機能の１つですので、長編や中編、短編連作などのシリーズものでも、作家が不要と感じる場合はタイトルの入力は必須ではありません。");
        } else if (index == "EDIT_CREATION") { this.showPopup("TIPS", "▼カテゴリー タグ", "作品投稿の際には一次創作（オリジナル）、二次創作のどちらかを必ず選択してください。\n二次創作作品を投稿する場合には原作品の知的財産権を侵害しないようお願いします。\n原作品において、二次創作についての公式ガイドラインがある場合には、当該公式ガイドラインを遵守してください。");
        } else if (index == "EDIT_CHARACTER") { this.showPopup("TIPS", "▼主人公 タグ", "作品投稿の際には「女主人公」「男主人公」「どちらでもない」のいずれかを必ず選択してください。\n\nなお、こちらの選択肢はあくまでも検索のしやすさのために設けているものであり、あらゆる性別に対する差別を助長するものではございません。\n精神的、身体的な特徴を問わず、作家が作品上どのように取り扱うかで決めてください。");
        } else if (index == "EDIT_RATING") { this.showPopup("TIPS", "▼レーティング タグ", "作品投稿の際には未成年への影響に配慮し、レーティングが適切かどうか慎重に判断してタグ、フィルター設定を行ってください。\n\n◆PG12\n・未成年による飲酒、喫煙、運転などの描写があるもの\n・未成年による暴力、薬物、詐欺、残酷描写があるもの\n・過度なホラー描写があるもの\n・上記に該当しないが、12歳以下の利用者に影響を及ぼす可能性のあるもの\n\n◆R-18\n・性行為、性的接触のあるもの\n・具体的、直接的な性器の名称、描写を用いているもの\n・具体的、直接的なアダルト用語を用いているもの\n・意図的に性的感情を刺激するもの\n・上記に該当しないが、未成年に影響を及ぼす可能性のあるもの\n\n◆R-18G\n・著しい暴力、薬物、詐欺、残酷描写のあるもの\n・上記に伴う、猟奇的に行われた身体の欠損、奇形の描写のあるもの\n・大量の出血、臓器や骨の露出\n・著しい排泄描写\n・上記に該当しないが、社会的に影響を及ぼす可能性のあるもの");
        } else if (index == "EDIT_FILTER") { this.showPopup("TIPS", "▼フィルター", "以下のいずれかのフィルターをかけることができます。\nこちらはあくまでも便利機能の１つですので、以下に当てはまる作品だったとしても、必ずしもフィルターをかける必要はありません。\n\n◆ネタバレ\n原作品の展開や結末などのネタバレが含まれている場合、「ネタバレ」のフィルターをかけてることができます。\n\n◆ワンクッション\n心の準備をしてほしいときに「ワンクッション」のフィルターをかけることができます。\n\n◆PG12\nレーティングタグで「PG12」を指定していても不安な場合、「PG12」のフィルターをかけることができます。\n\n◆R-18\nレーティングタグで「R-18」を指定していても不安な場合、「R-18」のフィルターをかけることができます。\n\n◆R-18G\nレーティングタグで「R-18G」を指定していても不安な場合、「R-18G」のフィルターをかけることができます。");
        } else if (index == "EDIT_TAGS") { this.showPopup("TIPS", "▼ジャンル タグ", "投稿作品のジャンル、作品傾向、注意書きなどをタグとして設定できます。\nこちらはお気に入りタグ、ミュートタグ、検索などで使用します。\n作家以外によるタグの追加、削除はできません。\n\n◆お気に入りタグ\n読者が自身の「設定」ページから、お気に入りのタグを登録できます。\nお気に入りのタグは検索ページに一覧表示され、該当のジャンルタグのついた作品が探しやすくなります。\n\n◆ミュートタグ\n読者が自身の「設定」ページから、ミュートタグを登録できます。\n苦手なジャンル、作品傾向を避けることができます。\n\n◆検索\n検索からジャンルタグの検索をすることができます。\nまた、作品一覧や閲覧ページに表示されているジャンルタグから、該当する作品を探すことができます。");
        } else if (index == "EDIT_PUBLISHING") { this.showPopup("TIPS", "▼公開方法", "以下のいずれかの公開方法を必ず選択してください。\n\n◆全体公開\nyumedropに登録しているすべての読者に公開されます。\n全体公開していても「検索一覧に載せる」の項目で「ＯＦＦ」にした場合、自動的に新着一覧、検索一覧に掲載されなくなります。\n\n◆Twitter相互フォロワー限定\nyumedropに登録している読者のうち、Twitterアカウントの相互フォロワーにのみ公開されます。\nTwitter相互フォロワー限定にした場合、自動的に新着一覧、検索一覧に掲載されなくなります。\n\n◆Twitterフォロワー限定\nyumedropに登録している読者のうち、Twitterアカウントのフォロワーにのみ公開されます。\nTwitterフォロワー限定にした場合、自動的に新着一覧、検索一覧に掲載されなくなります。\n\n◆Twitterリスト限定\nyumedropに登録している読者のうち、Twitterアカウントでリストに登録したユーザーにのみ公開されます。\nTwitterリスト限定にした場合、自動的に新着一覧、検索一覧に掲載されなくなります。\n\n◆パスワード\n投稿作品を読むためのパスワードのヒント、パスワードを設定できます。\n半角全角問わず、ヒントは50文字まで、パスワードは20文字まで入力できます。\nパスワード付きの設定にした場合、自動的に新着一覧、検索一覧に掲載されなくなります。\n\n◆非公開\nyumedropに登録しているすべての読者が読めなくなります。\n非公開の設定にした場合、自動的に新着一覧、検索一覧に掲載されなくなります。");
        } else if (index == "EDIT_SEARCHABLE") { this.showPopup("TIPS", "▼検索一覧に載せる", "検索一覧に載せない場合、新着一覧にも表示されません。\n　また、全体公開以外は、自動的に新着一覧、検索一覧に掲載されなくなります。");
        }
         else if (index == 'SETTING_PROFILE') {
          this.showPopup("TIPS", "▼プロフィール画像", "プロフィール画像を設定できます\n自分自身が著作権を有していない作品の場合、該当の利用規約に従ってください");
        } else if (index == 'SETTING_FAVORITE') {
          this.showPopup("TIPS", "▼お気に入りタグ", "読者がお気に入りのタグを登録できます\nお気に入りのタグは検索ページに一覧表示され、該当のジャンルタグのついた夢小説を探せます");
        } else if (index == 'SETTING_MUTE_TAG') {
          this.showPopup("TIPS", "▼ミュートタグ", "読者がミュートタグを登録できます\n読者が苦手なジャンル、作品傾向を避けることができます");
        } else if (index == 'SETTING_MUTE_USER') {
          this.showPopup("TIPS", "▼ユーザーミュート", "自分と趣味趣向、性的嗜好が合わないユーザーをミュートすることができます\nユーザーミュートすると、新着、検索一覧に該当ユーザーが表示されなくなります\nユーザーミュートを解除したい場合、「設定＞ミュートユーザー＞確認する」から、該当ユーザーのページに移動し、ユーザーミュートボタンを押して解除してください");
        } else if (index == 'READING_CONVERSION') {
          this.showPopup("TIPS", "▼変換項目", "夢小説に出てくる登場人物やキーワードを変換することができます\n入力欄に記入したワードは作者、運営に知られることはありません");
        }
      },
      chNull : function (val) {
        if (val == null) {
          return "";
        } else {
          return val;
        }
      },
      retComSeriesList : function(dataList) {
        let seriesList = [];
        seriesList.push("すべて");

        for (let i = 0; i < dataList.length; i++) {
          var series = dataList[i].series;
          var flg = true;
          for (let j = 0; j < seriesList.length; j++) {
            if (seriesList[j] == series) {
              flg = false;
            }
          }
          if (flg) {
            seriesList.push(series);
          }
        }
        return seriesList;
      },
      retComFilterList : function(dataList, series) {
        let filterDataList = [];
        for (let i = 0; i < dataList.length; i++) {
          var data = dataList[i];
          //シリーズの絞り込み
          if (series == "すべて" || data.series == series) {
            filterDataList.push(data);
          }
        }
        return filterDataList;
      },
      retComPagingList : function (page, dataList) {
        let retArray = [];
        if (dataList.length == 0) {
          return retArray;
        }

        let max = (page - 1) * 10 + 10;
        if (dataList.length < (page - 1) * 10 + 10) {
          max = dataList.length;
        }

        for (let i = (page - 1) * 10; i < max; i++) {
          retArray.push(dataList[i]);
        }
        return retArray;
      },
      checkAddress : function (mailAddress) {
        if (mailAddress.indexOf('@') == -1) {
          return 1;
        } else {
          let array = mailAddress.split('@');
          
          if (array.length != 2) {
            return 1;
          } else {
            let domain = array[1];

            if (domain.indexOf('.jp') == -1 && domain.indexOf('.com') == -1 && domain.indexOf('.net') == -1 && 
              domain.indexOf('.org') == -1 && domain.indexOf('.xyz') == -1) {
              return 2;
            }
          }
        }
        return 0;
      },
      checkPassword : function (password) {
        const regex = /^[a-zA-Z0-9!?&$%+-/#]+$/;
        return regex.test(password);
      },
      checkRegulation : function (str) {
        for (let i = 0; i < this.regulationArray.length; i++) {
          if (str.indexOf(this.regulationArray[i]) != -1) {
            return this.regulationArray[i];
          }
        }
        return "";
      }
    }
  }
</script>

